<template>
  <div>
    <base-query @queryhandle="query_handle">
      <el-row :gutter="20">
        <el-col :span="3">
          <div class="search-title"></div>
        </el-col>
        <el-col :span="9">
          <el-select v-model="searchForm.status" size="small" clearable placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </el-col>
        <el-col :span="3">
          <div class="search-title"></div>
        </el-col>
        <el-col :span="9">
          <el-input v-model="searchForm.phone1" style="width:300px;" size="small" placeholder=""></el-input>
        </el-col>
      </el-row>
    </base-query>
    <base-table tableName="" ref="table" :action="table.action" :columns="table.heads" :queryParams="queryParams">
      <template slot-scope="props">
        <div v-if="props.tplName=='tpstatus'">
          {{props.row.status==0?'':''}}
        </div>
        <div v-if="props.tplName=='toperation'">
          <el-button size="small" v-if="props.row.status==0" type="success" icon="el-icon-check" @click="setback_handle(props.row)"></el-button>
        </div>
      </template>
    </base-table>
    <el-dialog title="" :visible.sync="dialog" top="20vh" width="500px" :append-to-body="true" :close-on-click-modal="false" :before-close="handleClose">
      <el-card>
        <el-form label-width="100px" ref="form">
          <el-form-item label="">
            <el-input-number v-model="form.price" style="width:250px;" :min="1" :max="99999999" label=""></el-input-number>
          </el-form-item>
        </el-form>
      </el-card>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose"></el-button>
        <el-button type="primary" @click="save_handle"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchForm: {
        status: "",
        phone1: ""
      },
      queryParams: {
        status: "",
        phone1: ""
      },
      form: {
        price: 100
      },
      dialog: false,
      localrow: null,
      table: {
        action: "/api/crm/recommend",
        heads: [
          { prop: "id", label: "id", type: "data", width: 60 },
          { prop: "username1", label: "", type: "data" },
          { prop: "phone1", label: "", type: "data" },
          { prop: "username", label: "", type: "data" },
          { prop: "phone", label: "", type: "data" },
          { prop: "price", label: "", type: "data" },
          { prop: "createAt", label: "", type: "time" },
          { prop: "updateAt", label: "", type: "time" },
          { prop: "status", label: "", type: "comps", tplName: "tpstatus" },
          {
            prop: "operation",
            label: "",
            type: "comps",
            tplName: "toperation",
            width: 120
          }
        ]
      }
    };
  },
  methods: {
    query_handle() {
      Object.keys(this.searchForm).forEach(item => {
        if (this.searchForm[item])
          this.$set(this.queryParams, item, this.searchForm[item]);
        else delete this.queryParams[item];
      });
      this.$refs.table.reload();
    },
    handleClose() {
      this.dialog = false;
    },

    setback_handle(row) {
      this.dialog = true;
      this.localrow = row;
      this.form = {
        price: 100
      };
    },
    save_handle() {
      const postData = {
        status: 1,
        id: this.localrow.id,
        price: this.form.price
      };
      this.http.post("/api/crm/setrecommend", postData).then(res => {
        if (res.code == 200) {
          this.$message.success("");
          this.dialog = false;
          this.$refs.table.reload();
        } else {
          this.$message.error(res.msg);
        }
      });
    }
  }
};
</script>

<style>
</style>



