<template>
  <el-dialog title="" :visible.sync="formDialog" top="20vh" width="900px" :modal-append-to-body="false" :close-on-click-modal="false" :before-close="handleClose">
    <el-card>
      <el-form label-width="90px" ref="form">
        <el-form-item label="">
          <el-input v-model="form.name" disabled size="small" maxlength="50" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.username" disabled size="small" maxlength="50" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.phone" disabled size="small" maxlength="50" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.inviter" size="small" maxlength="50" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.inviterPhone" size="small" maxlength="50" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.inviterCode" disabled size="small" maxlength="50" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker v-model="form.entryAt" type="date" placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="form.isblacklist" disabled active-value="1" inactive-value="0" active-color="#13ce66" inactive-color="#ff4949" active-text="" inactive-text="">
          </el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.remarks" type="textarea" maxlength="100" :rows="3" placeholder="">
          </el-input>
        </el-form-item>
      </el-form>
    </el-card>

    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose"></el-button>
      <el-button type="primary" @click="induction_handle"></el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  props: {
    id: {
      type: Number,
      default: 0
    },
    formDialog: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isUpdate: false,
      form: {
        otherdata: []
      }
    };
  },
  methods: {
    handleClose() {
      this.$emit("dialogHandle", { dialog: false, isreload: this.isUpdate });
    },

    induction_handle() {
      if (!this.form.entryAt) {
        this.$message.warning("");
        return;
      }
      const postData = {
        id: this.id,
        unionid: this.form.unionid,
        status: 2,
        inviter: this.form.inviter,
        inviterPhone: this.form.inviterPhone,
        remarks: this.form.remarks,
        entryAt: this.form.entryAt
      };
      this.http.post("/api/crm/setenroll", postData).then(res => {
        if (res.code == 200) {
          this.$message.success("!");
          this.$emit("dialogHandle", { dialog: false, isreload: true });
        } else this.$message.error(res.msg);
      });
    }
  },
  watch: {
    formDialog() {
      if (this.formDialog) {
        this.http.get("/api/crm/wvisititem/" + this.id).then(res => {
          this.form = res.data;
        });
      }
    }
  }
};
</script>

<style>
</style>



