<template>
  <div>
    <base-query @queryhandle="query_handle">
      <el-row :gutter="20">
        <el-col :span="3">
          <div class="search-title"></div>
        </el-col>
        <el-col :span="9">
          <el-input
            v-model="searchForm.name"
            style="width:300px;"
            size="small"
            placeholder=""
          ></el-input>
        </el-col>
        <el-col :span="3">
          <div class="search-title"></div>
        </el-col>
        <el-col :span="9">
          <el-input
            v-model="searchForm.phone"
            style="width:300px;"
            size="small"
            placeholder=""
          ></el-input>
        </el-col>
      </el-row>
    </base-query>
    <base-table
      tableName=""
      ref="table"
      :action="table.action"
      :columns="table.heads"
      :queryParams="queryParams"
    >
      <div slot="button">
        <el-button
          size="small"
          icon="el-icon-plus"
          type="success"
          @click="add_handle"
        ></el-button>
      </div>
      <template slot-scope="props">
        <div v-if="props.tplName=='toperation'">
          <el-button
            size="small"
            v-if="props.row.name!=''"
            icon="el-icon-delete"
            type="danger"
            @click="editor_handle(props.row)"
          >
          </el-button>
          <el-button
            size="small"
            v-else
            icon="el-icon-delete"
            type="danger"
            @click="editor_handle(props.row)"
          >
          </el-button>
        </div>
        <div v-if="props.tplName=='tptype'">
          {{props.row.name==''?'':''}}
        </div>

      </template>
    </base-table>
    <el-dialog
      title=""
      :visible.sync="cashbackDialog"
      top="20vh"
      width="500px"
      :append-to-body="true"
      :close-on-click-modal="false"
      :before-close="handleClose"
    >
      <el-card>
        <el-form
          label-width="100px"
          ref="form"
        >
          <el-form-item label="">
            <el-select
              v-model="form.enrollid"
              filterable
              remote
              reserve-keyword
              placeholder=""
              :remote-method="remoteMethod"
              :loading="loading"
            >
              <el-option
                v-for="item in cashbacklist"
                :key="item.id"
                :label="item.username"
                :value="`${item.id+'|'+item.unionid}`"
              >
                <span style="float: left">{{ item.id }}</span>
                <span style="float: right; color: #8492a6; font-size: 13px">{{ item.username }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input-number
              v-model="form.price"
              style="width:250px;"
              :min="1"
              :max="99999999"
              label=""
            ></el-input-number>
          </el-form-item>
          <el-form-item label="">
            <el-input
              v-model="form.remarks"
              style="width:250px;"
              maxlength="100"
              type="textarea"
              :rows="3"
              placeholder=""
            >
            </el-input>
          </el-form-item>
        </el-form>
      </el-card>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleClose"></el-button>
        <el-button
          type="primary"
          @click="save_handle"
        ></el-button>
      </div>
    </el-dialog>
    <!--   <detail-form :formDialog2="formDialog" @dialogHandle="dialog_handle"></detail-form> -->
  </div>
</template>

<script>
/* import detailForm from "./form"; */
export default {
  /* components: {
    detailForm
  }, */
  data() {
    return {
      cashbackDialog: false,
      loading: false,
      cashbacklist: [],
      form: {
        price: 100,
        remarks: ""
      },
      searchForm: {
        name: "",
        phone: ""
      },
      queryParams: {
        enrollid: "",
        name: "",
        phone: ""
      },
      table: {
        action: "/api/cashback",
        heads: [
          { prop: "id", label: "id", type: "data", width: 60 },
          { prop: "name", label: "", type: "data" },
          { prop: "username", label: "", type: "data", width: 150 },
          { prop: "phone", label: "", type: "data", width: 150 },
          { prop: "createAt", label: "", type: "time", width: 120 },
          { prop: "remarks", label: "", type: "comps", tplName: "tptype" },
          { prop: "price", label: "", type: "data" },
          { prop: "remarks", label: "", type: "data" },
          {
            prop: "operation",
            label: "",
            type: "comps",
            tplName: "toperation",
            width: 120
          }
        ]
      }
    };
  },
  methods: {
    query_handle() {
      Object.keys(this.searchForm).forEach(item => {
        if (this.searchForm[item])
          this.$set(this.queryParams, item, this.searchForm[item]);
        else delete this.queryParams[item];
      });
      this.$refs.table.reload();
    },
    add_handle() {
      this.form = {
        price: 100,
        remarks: ""
      };
      this.cashbackDialog = true;
    },
    handleClose() {
      this.cashbackDialog = false;
    },
    remoteMethod(val) {
      this.http
        .get("/api/crm/enrolllist", { params: { phone: val, status: 3 } })
        .then(res => {
          this.cashbacklist = res.data;
          console.log("res=>phone=>", res);
        });
    },
    editor_handle(row) {
      let type = 0;
      let msg = ", ?";
      if (row.name == "") {
        type = 1;
        msg = ", ?";
      }
      this.$confirm(msg, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.http
            .delete("/api/cashback/" + row.id, { params: { type: type } })
            .then(res => {
              if (res.code == 200) {
                if (type == 0) this.$message.success("");
                else this.$message.success("");
                this.$refs.table.reload();
              } else this.$message.error(res.msg);
            });
        })
        .catch(() => {});
    },
    /*  dialog_handle(res) {
        this.formDialog = res.dialog;
    }, */
    save_handle() {
      if (!this.form.enrollid) {
        this.$message.warning("");
        return;
      }
      let arr = this.form.enrollid.split("|");
      let postData = {
        enrollid: arr[0],
        unionid: arr[1],
        price: this.form.price,
        remarks: this.form.remarks
      };
      this.http.post("/api/cashback", postData).then(res => {
        if (res.code == 200) {
          this.$message.success("");
          this.cashbackDialog = false;
          this.$refs.table.reload();
        } else this.$message.error(res.msg);
      });
    }
  }
};
</script>

<style>
</style>



