<template>
    <div>
        <base-query @queryhandle="query_handle">
            <el-row :gutter="20">
                <el-col :span="3">
                    <div class="search-title"></div>
                </el-col>
                <el-col :span="9">
                    <el-input v-model="searchForm.name" style="width:300px;" size="small" placeholder=""></el-input>
                </el-col>
                <el-col :span="3">
                    <div class="search-title"></div>
                </el-col>
                <el-col :span="9">
                    <el-input v-model="searchForm.phone" style="width:300px;" size="small" placeholder=""></el-input>
                </el-col>
            </el-row>
        </base-query>
        <base-table tableName="" ref="table" :action="table.action" :columns="table.heads" :queryParams="queryParams">
            <template slot-scope="props">
                <div v-if="props.tplName=='toperation'">
                    <el-button size="small" type="info" icon="el-icon-view" @click="view_handle(props.row)"></el-button>
                    <el-button size="small" icon="el-icon-edit" type="primary" @click="editor_handle(props.row,3)">
                    </el-button>
                    <el-button size="small" icon="el-icon-success" type="danger" @click="editor_handle(props.row,4)"></el-button>
                </div>
            </template>
        </base-table>
        <detail-form :formDialog="formDialog" :id="currid" @dialogHandle="dialog_handle"></detail-form>
    </div>
</template>

<script>
import detailForm from "./form";
export default {
  components: {
    detailForm
  },
  data() {
    return {
      currid: 0,
      formDialog: false,
      searchForm: {
        name: "",
        phone: "",
        status: 2
      },
      queryParams: {
        name: "",
        phone: "",
        status: 2
      },
      table: {
        action: "/api/crm/enrolllist",
        heads: [
          { prop: "id", label: "id", type: "data", width: 60 },
          { prop: "name", label: "", type: "data", width: 250 },
          { prop: "username", label: "", type: "data" },
          { prop: "phone", label: "", type: "data", width: 150 },
          { prop: "inviter", label: "", type: "data" },
          { prop: "inviterCode", label: "", type: "data" },
          { prop: "entryAt", label: "", type: "time" },
          {
            prop: "operation",
            label: "",
            type: "comps",
            tplName: "toperation",
            width: 280
          }
        ]
      }
    };
  },
  methods: {
    query_handle() {
      Object.keys(this.searchForm).forEach(item => {
        if (this.searchForm[item])
          this.$set(this.queryParams, item, this.searchForm[item]);
        else delete this.queryParams[item];
      });
      this.$refs.table.reload();
    },
    view_handle(row) {
      this.currid = row.id;
      this.formDialog = true;
    },
    editor_handle(row, status) {
      let msg = "?";
      if (status == 4) msg = "?";
      this.$confirm(msg, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          const postData = {
            id: row.id,
            unionid: row.unionid,
            status: status,
            inviter: row.inviter,
            inviterPhone: row.inviterPhone,
            remarks: row.remarks,
            entryAt: row.entryAt
          };
          this.http.post("/api/crm/setenroll", postData).then(res => {
            if (res.code == 200) {
              if (status == 3) this.$message.success("!");
              else this.$message.success("!");
              this.$refs.table.reload();
            } else this.$message.error(res.msg);
          });
        })
        .catch(() => {});
    },
    dialog_handle(res) {
      this.formDialog = res.dialog;
      if (res.isreload) this.$refs.table.reload();
    }
  }
};
</script>

<style>
</style>



